// stories/o-card.stories.tsx
import type { Meta, StoryObj } from '@storybook/web-components';
import { html } from 'lit';
import type { OTable } from '../components/o-table/o-table'; // Make sure to import your component

export default {
	title: 'Preview/Table'
} as Meta;

const Template: StoryObj<OTable> = {
	parameters: {
		layout: 'centered',
		backgrounds: {
			default: 'light' // Set light as the default background
		},
		docs: {
			source: {
				transform: () => {
					// Create dynamic code example using args
					return `
// Vue Code Snippet
-----------------------

import { OTable } from "@oui/vue";

<o-table>

</o-table>

// React Code Snippet
-----------------------

import { OTable } from "@oui/react";

<o-table>

</o-table>
          `;
				}
			}
		}
	},
	render: () => html` <o-table> </o-table> `
};

export const Default = {
	...Template,
	parameters: {
		layout: 'centered',
		backgrounds: {
			default: 'light' // Set light as the default background
		},
		docs: {
			description: {
				story:
					'A <strong>Card</strong> that includes everything available. This is includes a Card Head, a Subtitle, and Padding.'
			}
		}
	}
};
